import Vue from 'vue';
import Vuex from 'vuex';
import { latLng } from 'leaflet';
import compute from './funcs/compute.js';

Vue.use(Vuex);

const store = new Vuex.Store({
    state: {
        map: {
            center: latLng(62.601017, 29.763573),
            zoom: 16
        },
        showPoints: false,
        drawPath: false,
        selectPath: false,
        addMarker: false,
        moveMarker: false,
        test: 0,

        routeLength: 0,
        inter: null,

        markers: Array(10).fill(0).map(() => {
            return {
                latlng: latLng(62.601017 + Math.random() / 6 - 0.05, 29.763573 + Math.random() / 3 - 0.05),
                selected: false
            }
        }),
        customPath: []
    },
    mutations: {
        test() {
            console.log(store);
        },
        setCenter(state, latlng) {
            state.map.center = latlng;
        },
        computeSome(state) {
            const calc = compute(state.markers);
            state.markers = calc.route;
            state.length = calc.length;
            //state.markers = [];
        }
    },
    getters: {
        moveMarker: state => {
            return state.moveMarker;
        }
    }
});

export default store;





