


// Under the hood



const MUTATION_RATE = 0.25;
const MAX_MUTATION = 5;
const EVO_ITERATIONS = 100;
const ACCEPTED_SURVIVAL_RATE = 1;
const TOO_FEW_ROUTES = 0;
const MAX_ALLOWED_SPLITS = 10;
