const latlng = (lat, lng) => ({lat: lat, lng: lng});

const routePoint = (id, lat, lng) => ({
    id: id,
    latlng: latlng(lat, lng),
    selected: false
});

function calcDistance(p1, p2) {
    const R = 6373.0
            
    let lat1 = toRad(p1.latlng.lat)
    let lon1 = toRad(p1.latlng.lng)
    let lat2 = toRad(p2.latlng.lat)
    let lon2 = toRad(p2.latlng.lng)
    
    let dlon = lon2 - lon1
    let dlat = lat2 - lat1
    
    let a = Math.sin(dlat / 2)**2 + Math.cos(lat1) * Math.cos(lat2) * Math.sin(dlon / 2)**2
    let c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a))
    
    let distance = R * c
    
    return distance;
}

function toRad(Value) 
{
    return Value * Math.PI / 180;
}


function calcRouteLength(route) {
    let distance = 0;
        for (let i = 0; i < route.length - 1; i++) {

        distance += calcDistance(
            route[i],
            route[i + 1]
        );
    
    }

    return distance;
}


