function mutateRoute(route) {
    const n1 = Math.floor(Math.random() * route.length);
    const n2 = Math.floor(Math.random() * route.length);
    const tmp = route[n1];
    route[n1] = route[n2];
    route[n2] = tmp;
}

function buildRoutes(splittedRoutes) {
    const r = [];

    for (let i = 0; i < splittedRoutes.start.length; i++) {
        for (let j = 0; j < splittedRoutes.end.length; j++) {
            r.push([
                ...splittedRoutes.start[i],
                ...splittedRoutes.end[j]
            ]);
        }
    }

    return r;
}

function splitRoutes(routes) {

    const tmp = {
        start: [],
        end: []
    };

    const l = routes[0].length;

    let count = 0;

    //for (let r of routes) {
    for (let i = 0; i < routes.length; i++) {
        if (count > MAX_ALLOWED_SPLITS) {
            break;
        }
        const s = routes[i].slice(0, l / 2);
        const e = routes[i].slice(l / 2, routes[i].length);
        tmp.start.push(s);
        tmp.end.push(e);

        count++;
    }

    return tmp;
}


function reverseRoute(route) {
    const tmp = [...route];
    tmp.reverse();
    return tmp;
}


// ADD ROUTE LENGTH CHECK => ROUTE HAS EVERY ROUTEPOINT
function checkRoute(route, checkPoints) {

    let checkList = Array(checkPoints.length).fill(0).map((e, i) => ({ index: i, count: 0 }));

    for (let i = 0; i < checkList.length; i++) {
        checkList[route[i].id].count++;
    }

    for (let i = 0; i < checkList.length; i++) {
        for (let j = 0; j < checkList[i].count - 1; j++) {
            const removeIndex = route.findIndex(e => {
                if (e === null) {
                    return;
                }
                return e.id === checkList[i].index
            });
            route[removeIndex] = null;
        }
    }

    for (let i = 0; i < checkList.length; i++) {
        if (checkList[i].count === 0) {
            for (let j = 0; j < route.length - 1; j++) {
                const addIndex = route.findIndex(e => e === null);
                route[addIndex] = JSON.parse(JSON.stringify(checkPoints[checkList[i].index]));
                break;
            }
        }
    }

}

