<template>
    <div>
        <Info />
        <Map />
        <List :iniShow="true" :width='180' :header='"btn panel"' :iniPosition='{x: 100, y: 100}'>
            <InputGroup />
        </List>
        <List :header="'Route : Points'" :iniPosition='{x: 1200, y: 700}'>
            <div v-for='(m, index) in getMarkers' :key='index'
                v-bind:style='{background: m.selected ? "tomato" : "#555"}'
                @click='itemClick(m.latlng)'
            >
                <div v-bind:style='{padding: "10px", borderBottom: "2px solid #222"}'>
                    <div># marker {{ index }}</div>
                    <div class='small'>lat: {{ m.latlng.lat }} </div>
                    <div class='small'>lng: {{ m.latlng.lng }} </div>
                </div>
            </div>
        </List>

<!--         <List :header='"table #1"' :width='600'>
            <Table :data='getMarkers' :keys='["latlng", "selected"]'>
            </Table>
        </List> -->

        <List :header='"Routes / DEMO"' :width='600' :iniPosition='{x: 500, y: 50}'>
            <Table :data='generateData' :keys='["name", "routePoints", "nextRan"]'>
            </Table>
        </List>

        <List :header='"Search Result / DEMO : hits: " + searchResult.length' :width='600' :iniPosition='{x: 1200, y: 50}'>
            <Table :data='searchResult' :keys='["name", "routePoints", "nextRan"]'>
            </Table>
        </List>

        <List :header='"search"' :iniPosition='{x: 500, y: 700}'>
            <h5>route name:</h5>
            <input type='text' v-model='searchRouteName' placeholder='search by name...' />
            <!-- <input type='text' placeholder='second field...' /> -->
            <hr>
            <h5>days to next pickup:</h5>
            <input ref='dayToRanRange' min='0' max='100' type='range'
                v-model='searchDays'
            />
            <input v-bind:value='searchDays' type='text' readonly />
            <button @click='runDemoSearch'>go</button>
        </List>

    </div>
</template>

<script>
import { List, Map, Info, InputGroup, Table } from './components/index.js';
import store from './store.js';

export default {
    components: {
        List,
        Map,
        Info,
        InputGroup,
        Table
    },
    data() {
        return {
            searchRouteName: '',
            searchResult: [],
            searchDays: 30
        }
    },
    computed: {
        getMarkers() {
            console.log(store.state.markers);
            return store.state.markers;
        },
        generateData: {
            get() {
                const names = ['biojäte', 'raskasmetalli', 'huonekalujen', 'testipaja', 'roskalava', 'ruoka', 'lääke', 'aspfaltti', 'rengaskauppa', 's-marker', 'pahvi', 'ajelu', 'kierros', 'kaikki käy'];

                const action = ['keräys', 'tyhjennys', 'huolto', 'korjaus', 'reitti', 'yöreitti', 'päiväreitti', 'kerran vuodessa', 'kerran viikossa', 'kerran päivässä']


                const arr = Array(1000).fill(0).map(() => ({
                    name: names[Math.floor(Math.random() * names.length)] + ' ' + action[Math.floor(Math.random() * action.length)],
                    nextRan: Math.floor(Math.random() * 100 + 1),
                    routePoints: Math.floor(Math.random() * 10 + 5)
                }));

                return arr;
            }
        }
    },
    methods: {
        itemClick(latlng) {
            console.log(latlng.lat);
            store.state.map.center = latlng;
        },
        runDemoSearch() {
            const days = parseInt(this.$refs.dayToRanRange.value);
            let list = this.generateData.filter(e => e.nextRan <= days);

            if (this.searchRouteName !== '') {
                const reg = new RegExp(this.searchRouteName, 'g');
                list = list.filter(e => e.name.match(reg));
            }

            list = list.sort((a, b) => a.nextRan - b.nextRan);

            this.searchResult = list;
        }
    }
}
</script>

<style>
* {
    margin: 0;
    padding: 0;
}
body {
    font-family: Arial;
}
hr {
    border: 0.1px dashed #777;
    margin: 15px 0;
}
.small {
    font-size: 12px;
}

button {
    cursor: pointer;
}

input[type=text], select {
  width: 100%;
  padding: 12px 20px;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  font-size: 16px;
  margin: 4px 0;
}

input[type=range] {
    height: 60px;
    width: 100%;
}

input[type=text], active {
    outline: 0;
}


/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}
</style>