<template>
    <div
        class='container'
        v-bind:style='{left: x+"px", top: y+"px", width: width + "px"}'

    >
        <div
            draggable="true"
            @dragend='updateXY'
            @dragstart='calcStart'
            class='drag-zone'

            @mousedown='cleanSelect'
        >
            DRAG ZONE
        </div>
        <h4 style='padding-bottom: 10px; font-size: 20px;'>{{header}}</h4>

        <div v-if='show'
            v-bind:style='{maxHeight: "300px", overflow: "auto"}'
        >
            <slot></slot>
        </div>
        <hr>
        <div>
            <button @click='toggle'>
                <span>{{ show ? 'close' : 'open' }}</span>
            </button>
        </div>
    </div>    
</template>

<script>
import store from '../../store.js';

export default {
    props: {
        header: {
            type: String,
            default: 'header'
        },
        width: {
            type: Number,
            default: 300
        },
        iniShow: {
            type: Boolean,
            default: false
        },
        iniPosition: {
            type: Object,
            default() {
                return {
                    x: 0,
                    y: 0
                }
            }
        }
    },
    data() {
        return {
            startX: 0,
            startY: 0,
            x: 100,
            y: 100,
            show: true
        }
    },
    mounted() {
        this.show = this.iniShow;
        this.x = this.iniPosition.x;
        this.y = this.iniPosition.y;
    },
    computed: {
        getLength() {
            return store.state.routeLength;
        }
    },
    methods: {
        toggle() {
            this.show = !this.show;
        },
        calcStart(evt) {
            this.startX = evt.clientX - this.x;
            this.startY = evt.clientY - this.y;
            console.log('start : ', this.startX, this.startY);
        },
        updateXY(evt) {
            this.x = evt.clientX - this.startX;
            this.y = evt.clientY - this.startY;
        },
        cleanSelect() {
            // häksi
            if (window.getSelection) {window.getSelection().removeAllRanges();}
            else if (document.selection) {document.selection.empty();}
        }
    }
}
</script>

<style scoped>
.drag-zone {
    padding: 20px;
    text-align: center;
    cursor: grab;
    border: 2px dashed #555;
    margin-bottom: 20px;
}
.drag-zone:hover {
    border: 2px dashed cyan;
    background: #222;
}
.container {
    position: absolute;
    z-index: 1000;
/*     min-width: 400px; */
    padding: 20px;
    background: rgba(40, 40, 40, 0.95);
    color: #fff;
    box-shadow: 10px 10px 20px #000;
    border: 2px solid #000;
    border-radius: 5px;
    overflow: auto;
}

button {
    margin-top: 20px;
    padding: 10px;
}
</style>
